.container {
  position: relative;
  max-width: 1200px;
  margin: 0 auto;
  padding: 0 24px;
  background-color: transparent; }
  .container--flex {
    display: flex;
    flex-direction: column; }
  @media (max-width: 999px) {
    .container {
      padding-left: 16px;
      padding-right: 16px;
      margin: 0 auto; } }

.visually-hidden:not(:focus):not(:active),
input[type="checkbox"].visually-hidden,
input[type="file"].visually-hidden,
input[type="radio"].visually-hidden {
  position: absolute;
  overflow: hidden;
  clip: rect(0 0 0 0);
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  white-space: nowrap;
  border: 0;
  clip-path: inset(100%); }

picture {
  display: block; }

.heading-h1 {
  font-size: 36px;
  line-height: 46px;
  margin: 0;
  font-weight: 400;
  color: #000000; }
  @media (max-width: 999px) {
    .heading-h1 {
      font-size: 20px;
      line-height: 24px;
      font-weight: 700; } }
  @media (max-width: 699px) and (min-width: 320px) {
    .heading-h1 {
      margin-top: 10px; } }

.heading-h2 {
  font-size: 32px;
  line-height: 44px;
  margin: 0;
  font-weight: 400;
