.burger-button {
  margin-left: 10px;
  flex-shrink: 0;
  cursor: pointer;
  position: relative;
  display: inline-block;
  width: 16px;
  height: 26px;
  transform: translateY(2px);
  color: #000;
  transition: background-color 0.2s linear; }
  .burger-button:hover .burger-button__line {
    transition: background-color 0.2s linear;
    background-color: #ff7033; }

.burger-button__area {
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0; }

.burger-button__line {
  display: block;
  height: 2px;
  background-color: #000;
  margin-top: 5px; }

.logo {
  flex-shrink: 0;
  display: block;
  height: auto; }
  .logo .logo-pic {
    transition: fill 0.2s linear; }
    .logo .logo-pic:hover {
      transition: fill 0.2s linear;
      fill: #0087bf; }

.header__logo-pic {
  flex-shrink: 0;
  width: 106px;
  height: 40px;
  margin-right: 8px;
  transition: width 0.2s linear, height 0.2s linear; }

.banner {
