  color: #ffffff;
  padding: 18px 32px;
  margin-bottom: 32px; }
  @media (max-width: 999px) {
    .banner {
      align-items: flex-start; } }
  @media (max-width: 699px) and (min-width: 320px) {
    .banner {
      flex-direction: column; } }
  .banner:hover .banner__link-arrow {
    transition: transform 0.1s linear;
    transform: translateX(20%); }

.banner__link {
  display: inline-flex;
  flex-wrap: nowrap;
  align-items: center; }
  @media (max-width: 699px) and (min-width: 320px) {
    .banner__link {
      margin-top: 16px; } }

.banner__header {
  font-weight: normal;
  margin: 0;
  padding: 0;
  color: #ffffff;
  font-size: 24px;
  line-height: 30px;
  margin-right: 20px; }
  @media (max-width: 999px) {
